"use client";
import Image from "next/image";
import React from "react";

const Banner = () => {
  return (
    <div className="relative w-full h-[300px]">
      <Image
        src={require("../assets/image/banner.png")}
        alt="banner"
        objectFit="cover"
        fill
      />
    </div>
  );
};

export default Banner;
